export class MonacoEditor {
  editor: monaco.editor.IStandaloneCodeEditor;
  canvas: HTMLCanvasElement;
  ctx: CanvasRenderingContext2D;

  constructor(div: HTMLElement) {
    div.style.display = 'flex';

    const divLeft = document.createElement('div');
    div.appendChild(divLeft);
    divLeft.style.width = '30%';
    divLeft.style.height = '100%';
    divLeft.style.background = '#333';
    divLeft.id = 'dddd'

    var isResizing = false;
    var lastX = 0;

    divLeft.addEventListener('mousedown', function(e) {
      isResizing = true;
      lastX = e.clientX;
    });

    document.addEventListener('mousemove', function(e) {
      if (!isResizing) return;
      
      var delta = e.clientX - lastX;
      var newWidth = divLeft.offsetWidth + delta;
      
      divLeft.style.width = newWidth + 'px';
      
      lastX = e.clientX;
    });
    
    document.addEventListener('mouseup', function() {
      isResizing = false;
    });

    const divRight = document.createElement('div');
    div.appendChild(divRight);
    // divRight.style.width = '100%';
    // divRight.style.height = '100%';
    divRight.style.background = '#888';

    // const dd0 = document.createElement('div');
    // dd0.style.width = '100%';
    // dd0.style.height = '100px';
    // div.appendChild(dd0);

    const run = document.createElement('div');
    run.textContent = '>>'
    run.style.background = '#f00';
    run.style.width = '50px';
    run.style.height = '50px';
    run.addEventListener('click', this.runCode);
    div.appendChild(run);

    // const dd1 = document.createElement('div');
    // dd1.style.paddingTop = '20px';
    // dd1.style.width = '100%';
    // dd1.style.height = '50%';
    // dd1.id = 'editor';
    // div.appendChild(dd1);
    this.editor = initializeEditor(divLeft.id);

    // // // 실행 버튼 클릭 이벤트 리스너 등록
    // const initializeRunButton = () => {
    //     const runButton = document.createElement('div');
    //     runButton.style.width = '100px';
    //     runButton.style.height = '100px';
    //     runButton.style.position = 'absolute';
    //     div.appendChild(runButton);
    //     runButton.addEventListener('click', this.runCode);
    // };

    // // 초기화 함수 호출
    // initializeRunButton();


    // this.canvas = document.createElement('canvas');
    // this.canvas.width = 400;
    // this.canvas.height = 400;
    // div.appendChild(this.canvas);


    // Get the 2D rendering context
    // this.ctx = this.canvas.getContext('2d');

  }
  runCode = () => {
    const code = this.editor.getValue();
    const ctx = this.ctx;
    try {
      // eval(code);
    } catch (error) {
      console.error(error);
    }
  };
  destroy() {
    
  }
}

