const initializeEditor = (id: string = 'containerEditor') => {

  // monaco.languages.typescript.javascriptDefaults.setDiagnosticsOptions({
  //   noSemanticValidation: false,
  //   noSyntaxValidation: false,
  //   diagnosticCodesToIgnore: [/* top-level return */ 1108]
  // });

  // // Set up TypeScript compiler options
  // monaco.languages.typescript.typescriptDefaults.setCompilerOptions({
  //   target: monaco.languages.typescript.ScriptTarget.ES2015,
  //   allowNonTsExtensions: true
  // });

  // monaco.languages.typescript.javascriptDefaults.setCompilerOptions({
  //   target: monaco.languages.typescript.ScriptTarget.ESNext,
  //   allowNonTsExtensions: true
  // });

  const model = monaco.editor.createModel(code, 'typescript');
  const editor = monaco.editor.create(document.getElementById(id), {
    // value: 'console.log("Hello, Monaco Editor!");',
    model,
    value: code,
    language: 'typescript',
    minimap: { enabled: false },
    automaticLayout: true,
    contextmenu: false,
    
    fontSize: 16,
    scrollbar: {
      useShadows: false,
      vertical: "visible",
      horizontal: "visible",
      horizontalScrollbarSize: 12,
      verticalScrollbarSize: 12
    },
    theme: 'vs-dark',
  });

  // editor.langu

  monaco.editor.setModelLanguage(editor.getModel(), 'typescript');
  editor.updateOptions({ wordWrap: "on" })

  return editor;
};


