import { MonacoEditor } from './Monaco';

export class Solution {
  divHost: HTMLDivElement;
  monacoEditor: MonacoEditor;

  constructor(id: string) {
    const div = document.getElementById(id) as HTMLDivElement
    this.divHost = div;

    const divMain = document.createElement('div');
    divMain.style.background = '#f00';
    divMain.style.display = 'flex';
    divMain.style.width = '100%';
    divMain.style.height = '100%';
    this.divHost.appendChild(divMain)

    const divEditor = document.createElement('div');
    divEditor.style.width = '100%';
    divEditor.style.height = '100%';
    // divEditor.style.position = 'absolute';
    divEditor.id = 'containerEditor';
    divEditor.style.background = '#555';
    divMain.appendChild(divEditor)

    // const view = document.createElement('div');
    // view.style.width = '100%';
    // view.style.height = '100%';
    // // view.style.position = 'absolute';
    // view.id = 'containerEditor';
    // view.style.background = '#0f5';
    // div.appendChild(view)

    this.monacoEditor = new MonacoEditor(divEditor);
    // this.canvas = new CanvasCustom(view);
  }
  public destroy() {
    // window.removeEventListener('resize', this.resize);
    this.monacoEditor.destroy();
    try {
      while (this.divHost !== undefined && this.divHost.lastElementChild !== undefined) {
        this.divHost.removeChild(this.divHost.lastElementChild);
      }
    } catch (error) { }
  }
}
